
import { defineComponent, computed } from 'vue'

export default defineComponent({
  props: {
    isOpen: Boolean
  },
  emits: ['update:isOpen'],
  setup(props, { emit }) {

    let tempIsOpen = computed({
      get: () => props.isOpen,
      set: value => emit('update:isOpen', value),
    })

    const changeOpen = (): void => {
      tempIsOpen.value = !tempIsOpen.value
    }

    return {
      tempIsOpen,
      changeOpen
    }
  }
})
